import {Meteor} from 'meteor/meteor';
import {Utils} from './utils';

export class CollectionBase {

    constructor(collectionName,isOffline = false) {
        this.collectionName = collectionName;

        if(isOffline) {
            this.collectionInstance = new Ground.Collection(collectionName);
        }
        else {
            this.collectionInstance = new Mongo.Collection(collectionName);
        }

        this.collectionPermissions = {};
        this.collectionsDependents = [];


        this.schemaDefault = {};
        this.subSchemas = {};

        /**
         * DefiniÃ§Ãµes de seguranÃ§a
         * Deny all client-side updates on the MdlClientes collection
         */
        this.collectionInstance.deny({
            insert() {
                return true;
            },

            update() {
                return true;
            },

            remove() {
                return true;
            },
        });

        //endregion
    }


    getCollectionName() {
        return this.collectionName;
    }



    //region ####################### Hooks #######################
    afterUpdate(id, doc) {

        const listOfCollectionsDependents = this.collectionsDependents;
        const thisCollectionName = this.collectionName;

        //Itera
        for (let key in listOfCollectionsDependents) {
            let Schema = listOfCollectionsDependents[key].getSchemaJson();

            //Itera os campos da collection dependente estÃ¡ associado Ã  collection em alteraÃ§Ã£o
            //Para realizar a atualizaÃ§Ã£o
            for (let field in Schema) {
                if (typeof Schema[field].formOptions != 'undefined'
                    && typeof Schema[field].formOptions.OPTIONSCOLLECTION != 'undefined'
                    && Schema[field].formOptions.OPTIONSCOLLECTION.COLLECTION == thisCollectionName) {

                    //Define o filtro que serÃ¡ utilizado para
                    // realizar a atualizaÃ§Ã£o
                    let fieldFilter = JSON.parse('{"' + field + '._id": "' + id + '"}');

                    //Define a String de atualizaÃ§Ã£o considerando o
                    // campo que serÃ¡ atualizado como um array de objetos
                    let atualizar =
                        JSON.parse('{"$set": {"' + field + '.$": ' + JSON.stringify(doc) + '}}');

                    //Executa a atualizaÃ§Ã£o do campo
                    let resultUpdate = listOfCollectionsDependents[key].collectionInstance.update(fieldFilter, atualizar, {
                        upsert: false,
                        multi: true,
                    });

                    //console.log('{"' + field + '._id": "' + id + '"}');
                    //console.log('{"$set": {"' + field + '.$": ' + JSON.stringify(doc) + '}}');
                    //console.log(resultUpdate);
                }
            }

        }
    };

    canRemove(id) {
        const listOfCollectionsDependents = this.collectionsDependents;
        const thisCollectionName = this.collectionName;

        let result = true;

        for (let key in listOfCollectionsDependents) {
            let Schema = listOfCollectionsDependents[key].getSchemaJson();

            //Itera os campos da collection dependente estÃ¡ associado Ã  collection em alteraÃ§Ã£o
            //Para realizar a atualizaÃ§Ã£o
            for (let field in Schema) {
                if (typeof Schema[field].formOptions != 'undefined'
                    && typeof Schema[field].formOptions.OPTIONSCOLLECTION != 'undefined'
                    && Schema[field].formOptions.OPTIONSCOLLECTION.COLLECTION == thisCollectionName) {

                    //Define o filtro que serÃ¡ utilizado para
                    // realizar a pesquisa de documentos dependentes
                    let fieldFilter = JSON.parse('{"' + field + '._id": "' + id + '"}');

                    //Executa a pesquisa por documentos dependentes
                    let documentsDependent =
                        listOfCollectionsDependents[key].collectionInstance.find(fieldFilter).fetch();

                    //Se houver documento que depende do item a ser removido
                    // a remoÃ§Ã£o Ã© negada
                    if (documentsDependent.length > 0) {
                        //console.log('NÃ£o Ã© possÃ­vel excluir o documento pois ele estÃ¡ associado a outro documento');
                        result = false;
                        throw new Meteor.Error('OperaÃ§Ã£o nÃ£o realizada',
                            'NÃ£o serÃ¡ possÃ­vel realizar esta operaÃ§Ã£o pois este dado estÃ¡ associado a ' +
                            listOfCollectionsDependents[key].collectionName);
                    }
                }
            }

        }

        //O documento que serÃ¡ excluÃ­do nÃ£o estÃ¡ relacionado a nenhum outro documento.
        return result;
    };


    /**
     * Retorna o schema desejado
     * @param schemaName - Nome do Schema definido
     * @returns {SimpleSchema} - Retorna o schema passado por parÃ¢metro ou default
     * caso nenhum seja passado por parÃ¢metro
     */
    getSchema(schemaName = 'default') {
        let schema = {};
        if (schemaName === 'default' || typeof this.subSchemas[schemaName] == undefined) {
            schema = Utils.cloneObj(this.schemaDefault);
        } else {
            schema = this.getSubSchemaJson(schemaName);
        }

        for (let key in schema) {

            if (typeof schema[key].formOptions != 'undefined') {
                delete schema[key].formOptions;
            }

            if (typeof schema[key].formValidation != 'undefined') {
                delete schema[key].formValidation;
            }

            if (typeof schema[key].dataTableConfig != 'undefined') {
                delete schema[key].dataTableConfig;
            }
        }
        return new SimpleSchema(schema);
    }

    /**
     * Retorna o subSchema desejado
     * @param schemaName - Nome do subSchema definido
     * @returns {*} - Retorna o schema passado por parÃ¢metro ou default
     * caso nenhum seja passado por parÃ¢metro
     */
    getSubSchemaJson(schemaName = 'default') {
        let fields = [];
        if (typeof this.subSchemas[schemaName] != 'undefined')
            fields = this.subSchemas[schemaName];
        else
            console.log('O SubSchema ' + schemaName + ' NÃƒO existe!!!');

        let schema = Utils.cloneObj(this.schemaDefault);
        for (let key in schema) {
            if (fields.indexOf(key) == -1) {
                delete schema[key];
            }
        }
        return schema;

    }

    /**
     * Aplica um schema
     * @param schema - schema que serÃ¡ aplicado
     */
    setSchema(schema) {
        this.schemaDefault = schema;
        this.collectionInstance.attachSchema(this.getSchema());

    }

    /**
     * Inclui em uma lista uma collection que depende da atual
     * @param collection - Ã© a collection dependente
     */
    isRequiredBy(collection) {
        //console.log(collection.collectionName + ' depende da collection ' + this.collectionName);
        this.collectionsDependents.push(collection);

    }

    /**
     * Adiciona um subSchmea
     * @param schemaName -
     * @param schemaFields -
     */
    addSubSchema(schemaName, schemaFields) {
        this.subSchemas[schemaName] = schemaFields;

    }

    /**
     * Retorna um subSchema
     * @param schemaName -
     * @returns {*}
     */
    getSubSchema(schemaName) {
        return this.subSchemas[schemaName];
    }

    /**
     *  Retorna o schmema, em formato json, definido no modelo
     * @param schemaName
     * @returns {{}}
     */
    getSchemaJson(schemaName = 'default') {
        let schema = {};

        if (schemaName === 'default' || typeof this.subSchemas[schemaName] == undefined) {
            schema = Utils.cloneObj(this.schemaDefault);
        } else {
            schema = this.getSubSchemaJson(schemaName);
        }

        return schema;
    }

    /**
     *  Retorna o schema, em formato json para um determinado campo do documento
     * @param schemaName
     * @returns {{}}
     */
    getFieldSchemaJson(fieldName) {
        let schema = {};
        let fieldJson = {};

        schema = Utils.cloneObj(this.schemaDefault);

        if (schema[fieldName] && schema[fieldName].formOptions &&
            schema[fieldName].formOptions.FIELD_SCHEMA) {
            fieldJson = schema[fieldName].formOptions.FIELD_SCHEMA;
        }

        return fieldJson;
    }

    /**
     * Retorna a coleÃ§Ã£o
     * @returns {Mongo.Collection} - Retorna uma coleÃ§Ã£o
     */
    getCollection() {
        return this.collectionInstance;
    }

    /**
     * Retorna um objeto com as permissÃµes da Collection
     */
    getPermissions() {
        return this.collectionPermissions;
    }

    /**
     * Aplica as permissÃµes para aÃ§Ãµes por funcionalidade e por data
     * Essa funcionalidade sÃ³ pode ser aplicada no servidor
     * @param permissions permissÃµes definidas para a collection
     */
    setPermissions(permissions) {
        if (Meteor.isServer) {

            this.collectionPermissions = permissions;

            if (typeof permissions.byFunctionality != 'undefined') {
                for (let keyPerm in permissions.byFunctionality) {
                    this.collectionInstance.permit(permissions.byFunctionality[keyPerm].actions).ifHasRole(
                        permissions.byFunctionality[keyPerm].groups);
                }
            }

            if (typeof permissions.byData != 'undefined') {
                for (let keyPerm in permissions.byData) {

                    Security.defineMethod(this.collectionName + 'Permissions_' + keyPerm, {
                        fetch: [],
                        allow(type, field, userId, doc) {
                            let result = true;

                            for (let key in permissions.byData[keyPerm].data) {
                                if (doc[key] != permissions.byData[keyPerm].data[key])
                                    result = false;
                            }

                            return result && Roles.userIsInRole(userId, permissions.byData[keyPerm].groups);
                        },
                    });
                    this.collectionInstance.permit(permissions.byData[keyPerm].actions)[this.collectionName + 'Permissions_' + keyPerm]();
                }

            }

        }

    }

    /**
     * Retorna os campos de uma collection que serÃ£o exibidos
     * @param schemaName - Nome do schema que serÃ¡ aplicado
     * as regras de projection
     * @returns {{_id: number}} - Retorna estado dos campos
     * 0 nÃ£o serÃ¡ exibido e 1 serÃ¡ exibido
     */
    getProjection(schemaName) {
        let fields = [];
        if (this.getSubSchema(schemaName) != undefined) {
            fields = this.getSubSchema(schemaName);
        } else {
            for (let key in this.getSchemaJson()) {
                fields.push(key);
            }
        }

        //fields necessita ser um array de string
        if (!Array.isArray(fields)) {
            fields = [fields];
        }

        let projection = {_id: 1};
        fields.forEach(function (field) {
            projection[field] = 1;
        });

        return projection;

    }



}
;
