import {Meteor} from 'meteor/meteor';
import {Random} from 'meteor/random';
import {Utils} from './utils';

import {Counts} from 'meteor/tmeasday:publish-counts';

export class ModelBase {

    /**
     * Construtor
     * @param collectionBase - Collection que serÃ¡ passada para o construtor
     * @param isTest - Modo de teste
     */
    constructor(collectionBase, isTest = false) {

        this.myCollection = collectionBase.getCollection();

        this.myCollectionBase = collectionBase;

        this.collectionsModelDependents = [];

        this.functions = {};

        this.isTest = isTest;

        this.publications = function (filter, projection, tableConfig) {

            Counts.publish(this, collectionBase.getCollection()._name + "-counter", collectionBase.getCollection().find());


            var data = null;
            let userId = this.userId;
            let permissions = collectionBase.getPermissions();
            let queryParameter = {};

            if (typeof tableConfig != 'undefined')
                check(tableConfig, Object);

            check(projection, Object);
            // se existe um filtro

            if (projection != {})
                queryParameter["fields"] = projection;



            if (typeof filter === 'object') {
                check(filter, Object);

                if (permissions.length > 0) {
                    let result = true;

                    for (let keyPerm in permissions) {

                        //Se o usuÃ¡rio nÃ£o estiver no Grupo ele nÃ£o pode ver nada
                        if (permissions[keyPerm].groups != 'undefined'
                            && Roles.userIsInRole(userId, permissions[keyPerm].groups) == false && permissions[keyPerm].actions.indexOf('read') > -1) {
                            result = false;

                        } else if (permissions[keyPerm].groups != 'undefined'
                            && Roles.userIsInRole(userId, permissions[keyPerm].groups) == true && permissions[keyPerm].actions.indexOf('read') > -1) {
                            //Esta variÃ¡vel permite saber se o usuÃ¡rio em alguma das keyPerm possui direito 'read'
                            //Se a keyPerm atual chegar atÃ© aqui o result deve ser verdadeiro para o prÃ³ximo if
                            result = true;
                        }

                        //Altera o filtro para informar as permissÃµes de acesso
                        if (result && typeof permissions[keyPerm].data != 'undefined' && permissions[keyPerm].actions.indexOf('read') > -1) {
                            //Faz a substituiÃ§Ã£op da tag {_UserID_} pelo ID do usuÃ¡rio logado
                            let data = JSON.stringify(permissions[keyPerm].data);
                            if (typeof userId != 'undefined') {
                                data = data.replace(new RegExp('{_UserID_}', 'g'), userId);
                            }
                            filter = Utils.mergeObj(filter, JSON.parse(data));

                        }

                        if (result) {

                            //Define quais campos serÃ£o visiveis
                            if (filter != {})
                                queryParameter["filter"] = filter;

                            //Define propriedades da tabela: ordenaÃ§Ã£o, paginaÃ§Ã£o, etc.
                            if (typeof tableConfig != 'undefined' && Object.keys(tableConfig).length != 0) {
                                if (typeof tableConfig.limit != 'undefined')
                                    queryParameter["limit"] = tableConfig.limit;

                                if (typeof tableConfig.skip != 'undefined')
                                    queryParameter["skip"] = tableConfig.skip;

                                if (typeof tableConfig.sort != 'undefined')
                                    queryParameter["sort"] = tableConfig.sort;
                            }


                            return collectionBase.getCollection().find(filter, queryParameter);

                            //console.log('Publications Default');
                            //return collectionBase.getCollection().find();
                        }


                    }
                    console.log('NÃ£o publicou nada');
                    return this.ready();

                } else {
                    //Se nÃ£o existir permissÃµes Ã© retornado o filtro padrÃ£o
                    //Define quais campos serÃ£o visiveis
                    if (filter != {})
                        queryParameter["filter"] = filter;

                    //Define propriedades da tabela: ordenaÃ§Ã£o, paginaÃ§Ã£o, etc.
                    if (typeof tableConfig != 'undefined' && Object.keys(tableConfig).length != 0) {
                        if (typeof tableConfig.limit != 'undefined')
                            queryParameter["limit"] = tableConfig.limit;

                        if (typeof tableConfig.skip != 'undefined')
                            queryParameter["skip"] = tableConfig.skip;

                        if (typeof tableConfig.sort != 'undefined')
                            queryParameter["sort"] = tableConfig.sort;
                    }


                    return collectionBase.getCollection().find(filter, queryParameter);
                }

            } else {
                console.log('NÃ£o publicou nada');
                //Se o parÃ£metro filter for invÃ¡lido, ou seja, nÃ£o for um objeto, nÃ£o retorna nada.
                return this.ready();
            }

        };

        this.getUserId = () => {
            let userId = null;

            try {
                userId = Meteor.userId();
            }
            catch (err) {
                userId = this.userId;
            }

            return userId;
        }

        this.canUserDo = (action, doc) => {
            let permissions = this.myCollectionBase.getPermissions();
            let userId = this.getUserId();
            let result = false;
            if (userId === null) {

                //ToDo
                //Avaliar melhor essa situaÃ§Ã£o, pois se o usuÃ¡rio nÃ£o logado conseguir fazer uma
                //requsiÃ§aÃµ para o servidor ele poderÃ¡ prejudicar o sistema
                //Se nÃ£o existir userId o retorno Ã© true para permitir que o usuÃ¡rio se cadastre e logue.
                result = true;
            } else {
                if (permissions.length > 0) {
                    for (let index in permissions) {

                        if (typeof permissions[index].groups != 'undefined') {

                            if (Roles.userIsInRole(userId, permissions[index].groups)
                                && (permissions[index].actions.indexOf(action) > -1)) {
                                result = true;
                            }

                        }
                        //Verifica somente o acesso, o filtro de dados Ã© feito no Model
                        if ((result || typeof permissions[index].groups == 'undefined')
                            && ((typeof permissions[index].data != 'undefined') &&
                            (typeof doc != 'undefined') && (permissions[index].actions.indexOf(action) > -1))) {

                            //Substitui as tags {_UserID_} pelo ID do usuÃ¡rio no Objeto
                            let dataTmp = JSON.stringify(permissions[index].data);
                            permissions[index].data = JSON.parse(dataTmp.replace(new RegExp('{_UserID_}', 'g'), userId));

                            for (let field in permissions[index].data) {

                                //Se alguma condiÃ§Ã£o nÃ£o for aceita o resultado Ã© false
                                if (permissions[index].data[field] != doc[field]) {
                                    result = false;
                                }

                            }
                            if (result) {
                                return true;
                            }

                        } else if (result) {
                            return true;
                        }

                    }
                    //Se nenhuma permissÃ£o Ã© atendida o resultado Ã© sempre falso.
                    return result;

                } else {
                    //Se nÃ£o existir permissÃµes por funcionalidade (groups) ou data o acesso Ã© sempre permitido.
                    return true;
                }
            }

        }

        //Define uma variÃ¡vel para a funÃ§Ã£o de verificaÃ§Ã£o de permissÃµes
        let fCanUserDo = this.canUserDo;

        //######################################################
        //###InicializaÃ§Ã£o de mÃ©todos de validaÃ§Ã£o# de acesso###
        //######################################################

        this.functions['get.' + collectionBase.getCollection()._name + '.permissions']
            = function () {
            return collectionBase.getPermissions();
        };

        //##################################################
        //###InicializaÃ§Ã£o de mÃ©todos que alteram o banco###
        //##################################################


        this.functions[collectionBase.getCollection()._name + '.getTotalDocuments']
            = function () {

            let result = collectionBase.getCollection().find().count();
            return result;

        };

        this.functions[collectionBase.getCollection()._name + '.insert']
            = function (dataObj) {

            check(dataObj, collectionBase.getSchema('insert'));

            let result = collectionBase.getCollection().insert(dataObj);
            return result;

        };

        this.functions[collectionBase.getCollection()._name + '.update'] = function (id, dataObj) {
            check(id, String);
            check(dataObj, collectionBase.getSchema('update'));

            let result = collectionBase.getCollection().update(id, {
                $set: dataObj,
            });

            return result;

        };

        this.functions[collectionBase.getCollection()._name + '.upsert'] = function (id, dataObj) {
            check(id, String);
            //console.log(collectionBase.getSubSchemaJson('update'));
            check(dataObj, collectionBase.getSchema('update'));
            //console.log(dataObj);


            let result = collectionBase.getCollection().upsert(id, {
                $set: dataObj,
            });

            return result;

        };

        this.functions[collectionBase.getCollection()._name + '.remove'] = function (id) {
            check(id, String);

            let result = collectionBase.getCollection().remove(id);
            return result;

        };

        //####################### Hooks ##################################
        //####################### Hooks ##################################
        //####################### Hooks ##################################
        //####################### Hooks ##################################

        this.myCollection.before.remove(function (userId, doc) {

            if (fCanUserDo('remove', doc) == false) {
                throw new Meteor.Error('Acesso negado', 'VocÃª nÃ£o tem permissÃ£o para realizar esta operaÃ§Ã£o.');
                return false;
            } else {
                if (collectionBase.canRemove(doc._id))
                    return true;
            }

        });

        this.myCollection.before.update(function (userId, doc) {
            if (fCanUserDo('update', doc) == false) {
                throw new Meteor.Error('Acesso negado', 'VocÃª nÃ£o tem permissÃ£o para realizar esta operaÃ§Ã£o.');
                return false;
            } else
                return true;
        });

        this.myCollection.after.update(function (userId, doc) {
            collectionBase.afterUpdate(doc._id, doc);
        });


        this.myCollection.before.insert(function (userId, doc) {
            if (fCanUserDo('insert') == false) {
                throw new Meteor.Error('Acesso negado', 'VocÃª nÃ£o tem permissÃ£o para realizar esta operaÃ§Ã£o.');
                return false;
            } else {
                return true;
            }

        });

    }

    /**
     * Inicializa todos os mÃ©todos que sÃ£o executados no servidor, referente ao modelo.
     */
    applyAllMethods() {
        Meteor.methods(this.functions);
        return true;
    }

    /**
     * Retorna todos os mÃ©todos em uso
     * @returns {*|any}
     */
    getAllApplyMethods() {
        return Meteor.methods(this.functions);
    }

    getCollectionName() {
        return this.myCollection._name;
    }


    getCollection() {
        return this.myCollection;
    }

    /**
     * Inicializa o mÃ©todo informado
     * @param methodName Nome do mÃ©todo que serÃ¡ inicializado no servidor.
     */
    applyMethod(methodName) {
        Meteor.methods(this.functions[methodName]);
    }

    /**
     * Aplica os mÃ©todos que serÃ£o executados no servidor
     * @param methodName
     * @param functionDeclaration
     */
    setMethod(methodName, functionDeclaration) {
        this.functions[methodName] = functionDeclaration;
        this.applyMethod(methodName);
    }

    /**
     * Aplica as publicaÃ§Ãµes para a collection
     * @param newPublicationsFunction
     */
    setPublications(newPublicationsFunction) {
        this.publications = newPublicationsFunction;
        this.applyPublications();
    }

    /**
     * Faz a publicaÃ§Ã£o da collection
     */
    applyPublications() {

        Meteor.publish(this.myCollection._name, this.publications);


    }

    setPermissions(permissions = {}) {

        this.myCollectionBase.setPermissions(permissions);
    }

}
;
